<template>
  <div class="ui container">
    <barra></barra>
    <h1>Tandas</h1>
    <div
      class="ui segment divided items cont"
      :class="isRequest ? 'loading' : ''"
    >
      <ItemPelicula
        v-for="(item, index) in listTandas"
        @click.native="chageTanda(item)"
        :key="index"
        :item="item"
      >
      </ItemPelicula>
    </div>
    <ModalAdd :callbackRefresh="getTandas"> </ModalAdd>
    <ModalChange
      v-if="isChangeTandas"
      :itemData="currentSelected"
      :tandasRefresh="getTandas"
      :toggleChange="toggleChangeTandas"
    ></ModalChange>
  </div>
</template>

<script>
import axios from "../services/axios";
import Barra from "../components/Barra";
import ModalAdd from "../components/Tandas/ModalAdd";
import ModalChange from "../components/Tandas/ModalChange";
import ItemPelicula from "../components/ItemPelicula";

export default {
  name: "tandas",
  data: () => {
    return {
      isChangeTandas: false,
      isRequest: false,
      listTandas: [],
      currentSelected: {},
    };
  },
  methods: {
    toggleChangeTandas() {
      this.isChangeTandas = !this.isChangeTandas;
    },
    getTandas() {
      this.isRequest = true;
      axios.get("/api/tandas").then((res) => {
        this.isRequest = false;
        this.listTandas = res.data;
      });
    },
    chageTanda(itemdata) {
      this.currentSelected = itemdata;
      this.toggleChangeTandas();
    },
  },
  created() {
    this.getTandas();
    this.$store.commit("chageIndex", {
      newIndex: 0,
    });
  },
  components: {
    Barra,
    ModalAdd,
    ItemPelicula,
    ModalChange,
  },
};
</script>

<style lang="scss">
.ui.container {
  display: block;
}
.cont.segment {
  overflow: auto;
  height: 80vh;
  .item {
    cursor: pointer;
  }
  .item {
    transition: ease-in-out 0.2s;
  }
  .item:hover {
    transform: scale(1.01);
  }
}
</style>
