<template>
  <div class="fondo">
    <div class="ui container">
      <div
        class="ui segment animate__animated animate__fadeInDown"
        :class="isRequest ? 'loading' : ''"
      >
        <div class="ui form">
          <div class="field">
            <div class="ui header dividing">Editar tanda</div>
          </div>
          <div class="ui segment">
            <div class="field">
              <div class="ui toggle checkbox">
                <input type="checkbox" v-model="newData.estreno" />
                <label>Estreno</label>
              </div>
            </div>
          </div>
          <div class="field">
            <label>Title</label>
            <input type="text" placeholder="titulo" v-model="newData.title" />
          </div>
          <div class="field">
            <label>Imagen</label>
            <input type="file" accept="image/*" @change="changeImage" />
          </div>
          <div class="field">
            <label>Trailer link</label>
            <input
              type="text"
              placeholder="trailer link"
              v-model="newData.trailer"
            />
          </div>
          <div class="field">
            <label>Description</label>
            <textarea v-model="newData.description"></textarea>
          </div>
          <!-- btns -->
          <div class="fields">
            <div class="field">
              <button class="ui icon button red" @click="deleteTanda">
                <i class="trash icon"></i>
              </button>
            </div>
            <div class="field">
              <button class="ui icon button green" @click="updateTanda">
                <i class="save icon"></i>
              </button>
            </div>
            <div class="field">
              <button class="ui icon button" @click="toggleChange">
                <i class="cancel icon"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data: () => {
    return {
      newData: {
        title: null,
        description: null,
        image: null,
        portada: null,
        estreno: true,
        trailer: null,
      },
      isRequest: false,
    };
  },
  methods: {
    updateTanda() {
      if (confirm("Desea hacer los cambios")) {
        this.isRequest = true;

        let data = new FormData();
        data.append("id", this.itemData["id"]);
        data.append("title", this.newData.title);
        data.append("description", this.newData.description);
        data.append("estreno", this.newData.estreno);
        data.append("trailer", this.newData.trailer);

        if (this.newData.image) data.append("portada", this.newData.image);

        axios.put("/api/tandas", data).then((res) => {
          this.isRequest = false;
          if (res.data["status"] == 200) {
            this.tandasRefresh();
            this.toggleChange();
          }
        });
      }
    },
    deleteTanda() {
      if (confirm("Desea eliminar?")) {
        this.isRequest = true;
        axios.delete("/api/tandas/" + this.itemData["id"]).then((res) => {
          this.isRequest = false;
          if (res.data["status"] == 200) {
            this.tandasRefresh();
            this.toggleChange();
          }
        });
      }
    },
    changeImage(e) {
      this.newData.portada = URL.createObjectURL(e.target.files[0]);
      this.newData.image = e.target.files[0];
    },
  },
  created() {
    this.newData.title = this.itemData["title"];
    this.newData.description = this.itemData["description"];
    this.newData.estreno = this.itemData["estreno"];
    this.newData.portada = this.itemData["portada"];
    this.newData.trailer = this.itemData["trailer"];
  },
  props: {
    itemData: Object,
    tandasRefresh: Function,
    toggleChange: Function,
  },
};
</script>
<style lang="scss">
.fondo {
  width: 100vw;
  height: 100vh;
  background: rgba(0.5, 0.5, 0.5, 0.5);
  position: absolute;
  top: 0;
  left: 0;
}
</style>