<template>
  <div>
    <div class="ui ico button green add" @click="togglemodaladd()">
      <i class="plus icon"></i>
    </div>

    <div class="fondo" v-if="isShowModal">
      <div class="ui container">
        <div
          class="ui segment animate__animated animate__fadeInDown"
          :class="isRequest ? 'loading' : ''"
        >
          <div class="ui form">
            <div class="field">
              <div class="ui header dividing">Agregar nueva tanda</div>
            </div>
            <div class="ui segment">
              <div class="fields">
                <div class="field">
                  <div class="ui toggle checkbox">
                    <input type="checkbox" v-model="newData.estreno" />
                    <label>Estreno</label>
                  </div>
                </div>
                <div class="field">
                  <div class="ui toggle checkbox">
                    <input type="checkbox" />
                    <label>Doblada</label>
                  </div>
                </div>
              </div>
            </div>
            <div class="field">
              <label>Title</label>
              <input type="text" placeholder="titulo" v-model="newData.title" />
            </div>
            <div class="field">
              <label>Imagen</label>
              <input type="file" accept="image/*" @change="onChange" />
            </div>
            <div class="field">
              <label>Trailer link</label>
              <input
                type="text"
                placeholder="trailer link"
                v-model="newData.trailer"
              />
            </div>
            <div class="field">
              <label>Description</label>
              <textarea v-model="newData.description"></textarea>
            </div>
            <!-- btns -->
            <div class="fields">
              <div class="field">
                <input
                  class="ui blue button"
                  type="button"
                  value="Enviar"
                  @click="sendData()"
                />
              </div>
              <div class="field">
                <input
                  class="ui button cancel red"
                  type="button"
                  value="Cancelar"
                  @click="togglemodaladd()"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data() {
    return {
      isShowModal: false,
      isRequest: false,
      newData: {
        title: null,
        description: null,
        image: null,
        estreno: true,
        trailer: null,
      },
    };
  },
  methods: {
    togglemodaladd() {
      this.isShowModal = !this.isShowModal;
    },
    sendData() {
      if (
        this.newData.title &&
        this.newData.description &&
        this.newData.image &&
        this.newData.trailer
      ) {
        let data = new FormData();

        data.append("title", this.newData.title);
        data.append("description", this.newData.description);
        data.append("portada", this.newData.image);
        data.append("trailer", this.newData.trailer);
        data.append("estreno", this.newData.estreno);

        this.isRequest = true;
        axios.post("/api/tandas", data).then((res) => {
          this.isRequest = false;
          if (res.data["status"] == 200) {
            this.newData = {
              estreno: true,
            };
            this.togglemodaladd();
            this.callbackRefresh();
          }
        });

        // if(response.data['status'] == 200){

        // }
      }
    },
    onChange(e) {
      this.newData.image = e.target.files[0];
    },
  },
  props: {
    callbackRefresh: Function,
  },
};
</script>

<style lang="scss">
.fondo {
  width: 100vw;
  height: 100vh;
  background: rgba(0.5, 0.5, 0.5, 0.5);
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  justify-content: center;
  overflow: auto;
}
.button.add {
  position: absolute;
  bottom: 5px;
  right: 0;
}
</style>